@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;900&family=Roboto:ital,wght@0,300;0,400;0,500;1,300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root{
  --poppins: 'Poppins', sans-serif;;
  --roboto: 'Roboto', sans-serif;
}

body{
  height: 100vh;
}

.container{
  width: 100vw;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: .5rem;
  padding: 10px;
  
}

li.meaningLi::marker{
  font-family: var(--roboto);
  margin-right: 1rem;
  font-size: 14px;
  display: flex;
}

h1{
  font-size: 2rem;
  font-family: var(--roboto);
  color: dodgerblue;
  margin-bottom: 14px;
}

.information{
  display: flex;
  justify-content: space-between;
}

h2.about_h2{
  font-size: 1.5rem;
  font-weight: 400;
  font-family: var(--roboto);
}

p.about_p{
  font-family: var(--poppins);
  color: grey;
}

article{
  margin-bottom: 1.2rem;
}

@media screen and (max-width: 600px){
  html{
    font-size: 14px;
  }
}